h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 640px) {
  .container {
    padding: 0 40px;
  }
}

.header {
  scroll-behavior: smooth;
  padding-bottom: 70px;
  position: relative;
}

@media (width >= 640px) {
  .header {
    padding-bottom: 90px;
  }
}

@media (width >= 1280px) {
  .header {
    padding-bottom: 200px;
  }
}

.header__content {
  flex-direction: column-reverse;
  display: flex;
}

@media (width >= 640px) {
  .header__content {
    grid-template-columns: repeat(6, 1fr);
    align-items: center;
    column-gap: 30px;
    display: grid;
  }
}

.header__wrapper {
  background-image: url("bg-mobile.5e69f6df.jpg");
  background-position: center;
  background-size: cover;
  height: 300px;
  padding-top: 10px;
}

@media (width >= 640px) {
  .header__wrapper {
    background-image: url("bg-tablet.8022784a.jpg");
    grid-column: 4 / -1;
    height: 400px;
    padding-top: 15px;
  }
}

@media (width >= 1280px) {
  .header__wrapper {
    box-sizing: border-box;
    background-image: url("bg-desktop.20123d25.jpg");
    height: 100vh;
  }
}

@media (width >= 640px) {
  .header__logo {
    padding-left: 40px;
  }
}

.header__top {
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  column-gap: 20px;
  display: grid;
}

@media (width >= 640px) {
  .header__bottom {
    grid-column: 1 / 4;
    align-self: self-end;
    padding-bottom: 20px;
  }
}

@media (width >= 1280px) {
  .header__bottom-content {
    flex-direction: column;
    display: flex;
  }
}

.header__info {
  display: none;
}

@media (width >= 1280px) {
  .header__info {
    display: block;
    position: relative;
  }

  .header__info-desc {
    color: #687480;
    text-transform: uppercase;
    cursor: pointer;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
    text-decoration: none;
    display: inline-block;
    position: absolute;
    top: -110px;
    left: -20px;
    transform: rotate(-90deg);
  }

  .header__info-line {
    content: "";
    background-color: #1a5a4c;
    width: 1px;
    height: 70px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

.header__title {
  color: #000;
  text-align: center;
  margin: 20px 0;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 45px;
}

@media (width >= 640px) {
  .header__title {
    text-align: left;
    max-width: 400px;
    margin-bottom: 38px;
    font-size: 48px;
    line-height: 64.8px;
  }
}

@media (width >= 1280px) {
  .header__title {
    max-width: 700px;
    margin-bottom: 89px;
    font-size: 72px;
    line-height: 86.4px;
  }
}

.header__button {
  cursor: pointer;
  text-align: center;
  font-family: "IBM Plex Sans", sans-serif serif;
  color: #0f0e08;
  background-color: #fff;
  border: 1px solid #1a5a4c;
  align-content: center;
  width: 100%;
  height: 50px;
  padding-right: 30px;
  font-size: 16px;
  font-weight: 500;
  transition: all .3s;
  display: block;
  position: relative;
}

.header__button:hover {
  color: #fff;
  background-color: #1a5a4c;
}

.header__button:after {
  content: url("arrow-left.8b3877c7.svg");
  background-color: #1a5a4c;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

@media (width >= 640px) {
  .header__button:after {
    width: 70px;
  }

  .header__button {
    text-align: left;
    height: 70px;
    padding-left: 70px;
  }
}

@media (width >= 1280px) {
  .header__button {
    align-self: flex-end;
    width: 350px;
    margin-bottom: 118px;
    padding-left: 120px;
  }
}

.menu {
  opacity: 0;
  pointer-events: none;
  z-index: 99;
  background-color: #00000080;
  padding: 27px 0;
  transition: all .3s;
  position: fixed;
  inset: 0;
  transform: translateY(-100%);
}

.menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.menu:has(menu:target) {
  overflow-y: hidden;
}

@media (width >= 640px) {
  .menu {
    padding: 37px 0 40px;
  }
}

.menu__wrapper {
  background-color: #1a5a4c;
  width: 100%;
  height: auto;
  padding: 27px 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media (width >= 640px) {
  .menu__content {
    grid-template-columns: repeat(6, 1fr);
    gap: 30px;
    display: grid;
  }
}

@media (width >= 1280px) {
  .menu__content {
    grid-template-columns: repeat(12, 1fr);
  }
}

.menu__nav {
  gap: 28px;
  padding-bottom: 24px;
  display: flex;
  position: relative;
}

@media (width >= 640px) {
  .menu__nav {
    grid-column: span 3;
  }
}

@media (width >= 1280px) {
  .menu__nav {
    grid-column: span 5;
    justify-content: space-between;
  }
}

.menu__nav:after {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width >= 640px) {
  .menu__nav:after {
    width: 1px;
    height: 100%;
    left: 100%;
  }
}

@media (width >= 1280px) {
  .menu__nav:after {
    left: 120%;
  }
}

.menu__list {
  gap: 28px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu__items {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.menu__item--link {
  color: #fff;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
}

.menu__schedule {
  color: #fff;
  margin-top: 24px;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 19.69px;
}

@media (width >= 640px) {
  .menu__schedule {
    grid-column: 4 / -1;
    margin: 0;
  }
}

@media (width >= 1280px) {
  .menu__schedule {
    grid-column: 8 / -1;
  }
}

@media (width >= 640px) {
  .menu__schedule-wrapper {
    align-items: center;
    gap: 26px;
    display: flex;
  }

  .menu__schedule-day-off {
    max-width: 140px;
  }
}

.menu__schedule-title {
  margin: 0 0 20px;
  font-weight: 400;
}

.menu__schedule-day {
  gap: 5px;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  display: flex;
}

.menu__schedule-time {
  margin: 0;
  font-weight: 500;
  line-height: 21px;
}

.menu__schedule-name {
  margin: 0;
  line-height: 21px;
}

.icon {
  cursor: pointer;
  width: 24px;
  height: 24px;
  display: block;
  position: relative;
}

.icon-menu--open {
  background-image: url("menu-open.3804cec3.svg");
}

@media (width >= 640px) {
