  }

  .gallery__link--square {
    grid-column: span 4;
    padding-bottom: 100%;
  }

  .gallery__photo {
    object-fit: cover;
    width: 100%;
    height: auto;
    transition: all .3s ease-in;
    position: absolute;
  }

  .gallery__photo:hover {
    transform: scale(1.05);
  }
}

